<template><section><h1>Pagination</h1>
<template>
    <coding title="基本" content="基础的分页" :code="data1" lang="html">
        <y-pagination :total="5"></y-pagination>
    </coding>
    <coding title="超出" content="页数多于page-size" :code="data2" lang="html">
        <y-pagination :total="15"></y-pagination>
    </coding>
    <coding title="跳转" content="添加showQuickJumper" :code="data3" lang="html">
        <y-pagination :total="15" show-quick-jumper=""></y-pagination>
    </coding>
    <coding title="change" content="@currentChange在页数发生改变时执行，第一个参数是改变的页数(观察console)" :code="data4" lang="html">
        <y-pagination :total="5" @current-change="test"></y-pagination>
    </coding>
    <coding title="simple" content="simple模式" :code="data5" lang="html">
        <y-pagination :total="5" @current-change="test" simple=""></y-pagination>
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>total</td>
<td>总页数</td>
<td><code>Number</code></td>
<td>1</td>
<td>-</td>
</tr>
<tr>
<td>defaultCurrent</td>
<td>默认显示页数</td>
<td><code>Number</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>pageSize</td>
<td>分页数量</td>
<td><code>Number</code></td>
<td>5</td>
<td>-</td>
</tr>
<tr>
<td>showQuickJumper</td>
<td>是否显示跳转栏</td>
<td><code>Boolean</code></td>
<td>false</td>
<td></td>
</tr>
<tr>
<td>simple</td>
<td>simple模式</td>
<td><code>Boolean</code></td>
<td>false</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>current-change</td>
<td>页数改变时触发</td>
<td>当前页</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data () {
        return {
            data1: `<y-Pagination :total="5"></y-Pagination>`,
            data2: `<y-Pagination :total="15"></y-Pagination>`,
            data3: `<y-Pagination :total="15" show-quick-jumper></y-Pagination>`,
            data4: `<y-Pagination :total="5" @currentChange="test"></y-Pagination>`,
            data5: `<y-Pagination :total="5" @current-change="test" simple></y-Pagination>`
        }
    },
    methods: {
        test (e) {
            console.log(e)
        }
    }
}
</script>


